/**
 * This module executes inside of electron's main process. You can start
 * electron renderer process from here and communicate with the other processes
 * through IPC.
 *
 * When running `npm run build` or `npm run build:main`, this file is compiled to
 * `./src/main.js` using webpack. This gives us some performance wins.
 */
import { app, BrowserWindow, shell, ipcMain, dialog, Menu, webContents } from "electron";
import { autoUpdater } from "electron-updater";
import { resolveHtmlPath } from "./util";
const fs = require('fs');
const path = require('path');
import os from 'os'
import { spawn } from 'child_process';
import md5 from 'md5'
var RESOURCES_PATH;

const express = require('express');
const expressWs = require('express-ws');
const eapp = express();
expressWs(eapp);
const multer = require('multer');
var bodyparser = require('body-parser');

eapp.all('*', function (req, res, next) {
  res.header('Access-Control-Allow-Origin', '*');
  res.header('Access-Control-Allow-Headers', 'Content-Type');
  res.header('Access-Control-Allow-Methods', '*');
  res.header('Content-Type', 'application/json;charset=utf-8');
  next();
});

eapp.use(bodyparser.json({ "limit": '5000mb' }));

class AppUpdater {
  constructor() {
    log.transports.file.level = "info";
    autoUpdater.logger = log;
    autoUpdater.checkForUpdatesAndNotify();
  }
}

let mainWindow = null;

ipcMain.on("ipc-example", async (event, arg) => {
  const msgTemplate = pingPong => `IPC test: ${pingPong}`;
  console.log(msgTemplate(arg));
  event.reply("ipc-example", msgTemplate("pong"));
})

if (process.env.NODE_ENV === "production") {
  const sourceMapSupport = require("source-map-support");
  sourceMapSupport.install();
}

const isDebug =
  process.env.NODE_ENV === "development" || process.env.DEBUG_PROD === "true";

if (isDebug) {
  require("electron-debug")();
}

const installExtensions = async () => {
  const installer = require("electron-devtools-installer");
  const forceDownload = !!process.env.UPGRADE_EXTENSIONS;
  const extensions = ["REACT_DEVELOPER_TOOLS"];

  return installer
    .default(
      extensions.map(name => installer[name]),
      forceDownload
    )
    .catch(console.log);
}

const createWindow = async () => {
  if (isDebug) {
    await installExtensions();
  }

  RESOURCES_PATH = app.isPackaged
    ? path.join(process.resourcesPath, "assets")
    : path.join(__dirname, "../../assets")

  const getAssetPath = (...paths) => {
    return path.join(RESOURCES_PATH, ...paths);
  }

  mainWindow = new BrowserWindow({
    show: false,
    width: 1200,
    height: 728,
    minWidth: 1200,
    minHeight: 600,
    icon: getAssetPath("icon.png"),
    webPreferences: {
      // odeIntegration: true,
      // contextIsolation: false,
      // nodeIntegration: true,
      devTools: true,
      preload: app.isPackaged
        ? path.join(__dirname, "preload.js")
        : path.join(__dirname, "../../.erb/dll/preload.js")
    }
  })

  mainWindow.loadURL(resolveHtmlPath("index.html"));

  mainWindow.on("ready-to-show", () => {
    if (!mainWindow) {
      throw new Error('"mainWindow" is not defined');
    }
    if (process.env.START_MINIMIZED) {
      mainWindow.minimize();
    } else {
      mainWindow.show();
    }
  })

  mainWindow.on("closed", () => {
    mainWindow = null;
  })

  // const menuBuilder = new MenuBuilder(mainWindow)
  // menuBuilder.buildMenu()

  // Open urls in the user's browser
  mainWindow.webContents.setWindowOpenHandler(edata => {
    shell.openExternal(edata.url);
    return { action: "deny" };
  })

  // Remove this if your app does not use auto updates
  // eslint-disable-next-line
  new AppUpdater();
  Menu.setApplicationMenu(null);
}


app.on("window-all-closed", () => {
  // Respect the OSX convention of having the application in memory even
  // after all windows have been closed
  if (process.platform !== "darwin") {
    app.quit();
  }
})

app.whenReady()
  .then(() => {
    createWindow()
    app.on("activate", () => {
      // On macOS it's common to re-create a window in the app when the
      // dock icon is clicked and there are no other windows open.
      if (mainWindow === null) createWindow()
    })
  })
  .catch(console.log);


ipcMain.on('env', () => {
  mainWindow.webContents.send('env', app.isPackaged);
})


ipcMain.on('setBlenderPath', (evt, msg) => {
  dialog.showOpenDialog(mainWindow, {
    title: ' blender.exe',
    filters: [
      { name: '', extensions: ['exe'] }
    ],
    properties: ['openFile']
  }).then(result => {
    const filePaths = result.filePaths
    mainWindow.webContents.send('setBlenderPath', filePaths[0].replaceAll('\\', '/'));
  }).catch(err => {
    console.error(':', err);
  });
})

ipcMain.on('getBlenderVer', (e, m) => {
  var blender = spawn(m.path, ['-b', '-v'], {
  });

  blender.stdout.on('data', (m) => {
    mainWindow.webContents.send('getBlenderVer', m.toString('utf8'))
  })
})

ipcMain.on('setOutPath', (evt, msg) => {
  dialog.showOpenDialog({
    title: "", // 
    properties: ['openDirectory'] // 
  }).then(result => {
    if (!result.canceled && result.filePaths.length > 0) {
      const filepath = result.filePaths[0].replaceAll('\\', '/');
      mainWindow.webContents.send('setOutPath', filepath);
    }
  }).catch(err => {
    console.error(err);
  });
});

ipcMain.on('saveFile', (e, m) => {
  const { base64String, filePath, fileName } = m;
  const base64Data = base64String.replace(/^data:image\/\w+;base64,/, '');
  const buffer = Buffer.from(base64Data, 'base64');
  if (!fs.existsSync(filePath)) {
    fs.mkdirSync(filePath);
  }
  fs.writeFile(`${filePath}\\${fileName}`, buffer, (err) => {
    if (err) {
      console.error(':', err);
      mainWindow.webContents.send('saveFile', { success: false, data: err });
      return;
    }
    mainWindow.webContents.send('saveFile', { success: true, data: `${filePath}/${fileName}` });
    console.log(':', `${filePath}/${fileName}`);
  });
});

ipcMain.on('getIP', (e, m) => {
  mainWindow.webContents.send('getIP', os.networkInterfaces())
})

var config, server;
ipcMain.on('startServer', (e, m) => {
  config = m;
  if (!(server == null)) {
    server.close(() => {
      console.log('Server is closed');
      mainWindow.webContents.send('startServer', { success: false })
    });
    return;
  }
  onServerInit();
  server = eapp.listen(m.port, () => { mainWindow.webContents.send('startServer', { success: true }) });
})


function onServerInit() {

}

eapp.post('/upload', (req, res) => {
  const storage = multer.diskStorage({
    destination: config.outPath,
    filename(ctx, file, cb) {
      cb(null, `temp.blend`);
    }
  });
  const upload = multer({ storage }).single('file');
  upload(req, res, function (err) {
    if (err) {
      // 
      console.error(err);
      res.status(500).send('');
      return;
    }

    const { password } = req.body;

    if (password !== md5(config.password)) {
      res.status(401).send('');
      return;
    }

    mainWindow.webContents.send('blenderOut', '');
    console.log(req.file);
    mainWindow.webContents.send('blenderOut', '');
    res.status(200).send('');
  });
});

eapp.ws('/ArBlender', function (ws, req) {
  const { password, command } = req.query;
  if (password !== md5(config.password)) {
    ws.send(JSON.stringify({ auth: 'false' }));
    ws.close(); // 
    return;
  }

  mainWindow.webContents.send('client', req.connection.remoteAddress);
  ipcMain.on('closeWSC', () => {
    ws.close()
  })
  ws.on('message', function (data) {
    var msg = JSON.parse(data);
    console.log(msg);
    if (command) {
      render(ws, msg);
    }

  });

  ws.on('close', function (e) {
    console.log('', e);
    mainWindow.webContents.send('client', null);
  });
});


function render(ws, msg) {
  const { format, startFrame, endFrame } = msg
  console.log('');
  var arblender = spawn(config.blenderPath,
    ['-b', `${config.outPath}\\temp.blend`,
      '-o', `${config.outPath}\\output\\${msg.fileName}.${msg.format}`,
      '-F', format,
      '-s', startFrame,
      '-e', endFrame, '-a']
  )

  arblender.stderr.on('data', function (data) {
    var log = {
      success: true,
      type: 'error',
      logs: data.toString('utf8')
    }
    ws.send(JSON.stringify(log));
    console.log(log)
    mainWindow.webContents.send('blenderErr', data.toString('utf8'))
  });

  arblender.stdout.on('data', function (data) {
    var log = {
      success: true,
      type: 'log',
      logs: data.toString('utf8')
    }
    ws.send(JSON.stringify(log));
    console.log(log)
    mainWindow.webContents.send('blenderOut', data.toString('utf8'))

  });

  arblender.on('exit', function (code, signal) {
    var log = JSON.stringify({
      success: true,
      type: 'exit',
      logs: code,
      signal: signal
    });
    ws.send(log);
  });

}
